* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Varela Round", sans-serif;
}
h3,
h4 {
  font-weight: 400;
  color: #5e5b5b;
  letter-spacing: 0.7px;
}
.App {
  transition: all ease 0.5s;
}
.library-active {
  margin-left: 20%;
}
@media screen and (max-width: 950px) {
  .library-active {
    margin-left: 30%;
  }
}
@import "player";
@import "song";
@import "library";
@import "librarysong";
@import "nav";
